@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
    font-family: "Muli";
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Muli Regular"), local("Muli-Regular"),
        url(https://fonts.gstatic.com/s/muli/v16/7Auwp_0qiz-afTLGKA.ttf)
            format("truetype");
}
@font-face {
    font-family: "Muli";
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: local("Muli Bold"), local("Muli-Bold"),
        url(https://fonts.gstatic.com/s/muli/v16/7Au_p_0qiz-adYnjOCX2yg.ttf)
            format("truetype");
}
header {
    background-color: white;
}
header a {
    padding: 0px;
    margin: 0px;
    color: #19345e;
}
header .header-logo {
    margin: 11px 0px 11.73px 0px;
}
header .header-menu {
    align-items: center;
    display: table;
    text-align: right;
    font-family: Muli, sans-serif;
    font-size: 13px;
    padding: 0px;
}
header .header-menu ul {
    font-size: 13px;
    text-decoration: none;
}
header .header-menu ul li {
    display: table-cell;
    vertical-align: middle;
    height: 100%;
    padding: 34.5px 10px 34.4px 10px;
}
header .header-menu ul li a {
    padding: 0px;
    margin: 0px;
    display: table;
    font-size: 20px;
}

.homeground {
    font-family: "Lobster", cursive;
    height: 80vh;
}
.box-biru {
    background-color: rgba(25, 52, 94, 0.65);
    position: absolute;
    background-size: cover;
    width: 100%;
    height: 650px;
    opacity: 65%;
}
.login {
    font-family: "Lobster", cursive;
}
body {
    font-family: Muli, sans-serif;
    background-image: #f3f3f1;
}
/* test */
.kategori {
    align-items: center;
    display: table;
    text-align: right;
    font-family: Muli, sans-serif;
    font-size: 13px;
    padding: 0px;
}
.kategori ul {
    font-size: 13px;
    text-decoration: none;
}
.kategori ul li {
    display: table-cell;
    vertical-align: middle;
    height: 100%;
    padding: 10px;
    font-weight: 400;
}

@media (max-width: 1280px) {
    .header-menu {
        text-align: right;
        float: none;
    }
    .header-logo {
        text-align: center;
        height: 50px;
    }
    .header-logo img {
        text-align: center;
        height: 50px;
    }
    .kategori ul li {
        font-size: 8px;
    }
    /* header {
    height: 50px;
  } */

    header .header-menu ul li {
        padding: 3px;
    }
    .search {
        float: none;
    }
    #notfoundpic {
        width: 300px;
    }

    #left-image {
        display: none;
    }
}

